.wrapper {
  position: relative;

  display: flex;
  justify-content: center;
  flex-direction: column;

  width: 100%;
  height: 100%;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  overflow: hidden;
}

.wrapper::after {
  z-index: 1;
  content: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to bottom,
    #000,
    #000 15%,
    transparent 30%,
    transparent 70%,
    #000 85%,
    #000
  );
}

@media (max-width: 991px) {
  .wrapper {
    justify-content: flex-start;
    background-size: 1000px 600px;
  }
  .wrapper::after {
    content: "";
  }
  .wrapperRight .inner {
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .wrapper {}
}

@media (max-width: 600px) {
  .wrapper {
    background-position: right -270px center;
  }
  .wrapperRight {
    background-position: right -200px center;
  }
}

@media (max-width: 500px) {
  .wrapperRight {
    background-position: right -170px center;
  }
}

@media (max-width: 400px) {
  .wrapperRight {
    background-position: right -230px center;
  }
}

.overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200%;
  margin-left: 100px;
  padding-bottom: 270%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background-image: radial-gradient(
    transparent,
    rgba(0, 0, 0, 0.1) 30%,
    rgba(0, 0, 0, 1) 50%,
    rgba(0, 0, 0, 0.7)
  );
}

@media (max-width: 991px) {
  .overlay {
    display: block;
  }
}


/* content */

.fixedWrapper {
  position: fixed;
  z-index: 10;

  top: 50%;
  left: 6.6%;

  width: 100%;
  height: 250px;

  max-width: 600px;

  overflow: hidden;

  transform: translateY(-50%);
  opacity: 0;
  pointer-events: none;
}

.shown {
  opacity: 1;
}

.fixedWrapper .fixedInner {
  max-width: 500px;
}

@media (max-width: 1199px) {
  .fixedWrapper .fixedInner {
    max-width: 400px;
  }
}

@media (max-width: 991px) {
  .fixedWrapper .fixedInner {
    max-width: 320px;
    padding-top: 20px;
  }
}

@media (max-width: 700px) {
  .fixedWrapper {
    top: 0;
    transform: translateY(0);
    height: 140px;
    max-width: 270px;
  }
}

.scrollContainer {
  width: 100%;
  transition: all .3s;
  pointer-events: none;
}

.earn {
  transform: translateY(125px);
}

@media (max-width: 700px) {
  .earn {
    transform: translateY(0);
  } 
}

.dex {
  transform: translateY(-175px);
}

.pawnshop {
  transform: translateY(-475px);
}

.fixedWrapper .fixedInner {
  position: absolute;

  top: 50%;

  transform: translateY(-50%);
}

@media (max-width: 700px) {
  .fixedWrapper .fixedInner {
    top: 0;
    transform: translateY(0);
  }
}

.fixedWrapper .fixedInner:nth-child(2) {
  transform: translateY(-50%) translateY(300px);
}

@media (max-width: 700px) {
  .fixedWrapper .fixedInner:nth-child(2) {
    transform: translateY(175px);
  }
}

.fixedWrapper .fixedInner:nth-child(3) {
  transform: translateY(-50%) translateY(600px);
}

@media (max-width: 700px) {
  .fixedWrapper .fixedInner:nth-child(3) {
    transform: translateY(475px);
  }
}

/*  */

.inner {
  position: absolute;
  top: 50%;
  left: 6.6%;
  z-index: 2;
  max-width: 500px;
  transform: translateY(-50%);
}

@media (max-width: 1199px) {
  .inner {
    max-width: 400px;
  }
}

@media (max-width: 991px) {
  .inner {
    max-width: 320px;
    padding-top: 20px;
  }
}

@media (max-width: 700px) {
  .inner {
    top: 0;
    max-width: 270px;
    transform: translateY(0);
  }
}

