.wrapper {
    display: flex;
    flex-wrap: wrap;
}

@media (max-width: 860px) {
    .wrapper {
        justify-content: space-between;
    }
}

.group {
    margin-right: 60px;
}

@media (max-width: 550px) {
    .group {
        width: 50%;
        margin-right: 0;
        margin-bottom: 25px;
    }
}

.group:last-child {}

@media (max-width: 991px) {
    .group:last-child {
        margin-right: 0;
    }   
}

.title {
    margin-top: 0;
    margin-bottom: 16px;

    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 150%;
}

@media (max-width: 1600px) {
    .title {
        margin-bottom: 10px;
    }
}

@media (max-width: 550px) {
    .title {
        margin-bottom: 8px;
    }
}

.menu {
    list-style-type: none;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
}

.menuItem {
    margin-bottom: 6px;

    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;

    opacity: 0.6;
}

.menuItem:hover {
    text-decoration: underline;
}

@media (max-width: 1600px) {
    .menuItem {
        margin-bottom: 8px;
        font-size: 16px;
    }
}

@media (max-width: 550px) {
    .menuItem {
        margin-bottom: 5px;
    }
}

.menuItem:last-child {
    margin-bottom: 0;
}

.menuItem a {
    color: #FFF;
    text-decoration: none;
}