.wrapper {
  height: 100%;
  z-index: 1;
  background-image: url('./bg.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.wrapper::after {
  z-index: 2;
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.8;
  background-color: #171717;
  pointer-events: none;
  animation: Hide 0.2s ease-out;
  animation-fill-mode: forwards;
}

@keyframes Hide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.background {
  z-index: 2;

  position: absolute;

  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background: linear-gradient(
    180deg,
    rgba(23, 23, 23, 0.5) 0%,
    rgba(23, 23, 23, 0.1) 20%,
    rgba(23, 23, 23, 0) 40%,
    rgba(23, 23, 23, 0) 60%,
    rgba(23, 23, 23, 0.3) 80%,
    rgba(23, 23, 23, 0.8) 100%
  );
}

.content {
  position: relative;
  height: 100%;
  z-index: 3;
}

.video {
  z-index: 2;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
}

.videoShown {
  opacity: 1
}
