.wrapper {
    padding-top: 100px;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media (max-width: 1400px) {
    .wrapper {
        display: block;
    }
}

@media (max-height: 1215px) {
    .wrapper {
        display: block;
    }
}

.animateRoadmap {
    animation: Show 0.7s ease-out forwards;
}

@keyframes Show {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0);
    }
}

@media (max-width: 1400px) {
    .wrapper {
        padding-top: 90px;
        padding-top: 70px;
    }
}

@media (max-height: 1100px) {
    .wrapper {
        padding-top: 50px;
    }
}

.inner {
    position: relative;
    padding-top: 90px;
}

@media (max-width: 1400px) {
    .inner {
        padding-top: 80px;
    }
}

@media (max-height: 1100px) {
    .inner {
        padding-top: 40px;
    }
}

@media (max-width: 700px ) {
    .inner {
        padding-top: 60px;
        padding-top: 30px;
    }
}