.wrapper {
  padding-top: 345px;
  padding-bottom: 345px;
  background-position: center;
  background-size: cover;
  background-image: url("./bg.jpg");
  background-color: #1e2840;
}

@media (max-width: 1600px) {
  .wrapper {
    padding-top: 300px;
    padding-bottom: 300px;
  }
}

@media (max-width: 767px) {
  .wrapper {
    background-image: url("./bg-mobile.jpg");
  }
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 767px) {
  .controls {
    flex-direction: column;
  }
}

.control {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 278px;
  height: 74px;

  margin: 0 25px;

  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 90%;
  text-decoration: none;

  border: 1px solid #3a6ee8;
  border-radius: 5px;

  background: #1c2e58;
  color: #d8e2ff;

  transition: all .2s;
  cursor: pointer;
}

.control:hover {
  box-shadow: inset 0 0 8px hsl(0deg 0% 100% / 25%);
  border-color: #04a8f0;
}

@media (max-width: 1600px) {
  .control {
    width: 295px;
    height: 70px;
    margin: 0 10px;
    font-size: 24px;
  }
}

@media (max-width: 767px) {
  .control {
    margin: 0;
    margin-bottom: 30px;
  }
}

@media (max-width: 480px) {
  .control {
    width: 150px;
    height: 56px;
    font-size: 18px;
  }
}

.control:last-child {
  background: #1e2840;
}

@media (max-width: 767px) {
  .control:last-child {
    margin-bottom: 0;;
  }
}
