.showPromo {
    opacity: 1;

    position: fixed;

    z-index: 10;

    bottom: 0;
    left: 0;

    width: 100%;
    height: 100%;

    /* background-color: rgba(43, 43, 67, 1); */
    /* transform: translateY(100%); */
    /* animation: MoveUpDiv 0.6s ease-in-out forwards; */
}

