.showPromo div:nth-child(1) {
    position: fixed;

    z-index: 11;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: rgba(43, 43, 67, 0.5);
    transform: translateY(100%);
    animation: MoveUpDiv1 0.916s ease-in forwards;
    /* animation: MoveUpDiv1 0.458s ease-in forwards; */
    /* animation: MoveUpDiv1 4.58s ease-in forwards; */
    /* animation: MoveUpDiv1 0.3435s ease-in-out forwards; */
}

@keyframes MoveUpDiv1 {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(-100%);
    }
}

.showPromo div:nth-child(2) {
    position: fixed;

    z-index: 11;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: rgba(43, 43, 67, 1);
    transform: translateY(100%);
    animation: MoveUpDiv2 0.5s .175s ease-in forwards;
    animation: MoveUpDiv2 0.5s .350s ease-in forwards;
    /* animation: MoveUpDiv2 5s 1.25s ease-in-out forwards; */
    /* animation: MoveUpDiv2 0.375s .09375s ease-in-out forwards; */
}

@keyframes MoveUpDiv2 {
    0% {
        transform: translateY(100%);
    }
    /* 20% {
        transform: translateY(90%);
    } */
    100% {
        transform: translateY(-100%);
    }
}


.showPromo div:nth-child(3) {display: none;
    position: fixed;

    z-index: 11;

    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: #171717;
    transform: translateY(100%);
    animation: MoveUpDiv3 0.78s cubic-bezier(0.96,0.01,0.91,0.26) forwards;
    animation: MoveUpDiv3 0.78s cubic-bezier(0.75,0.016,0.91,0.026) forwards;
    /* animation: MoveUpDiv3 0.78s cubic-bezier(0.96,0.01,0.99,0.2) forwards; */
    /* animation: MoveUpDiv3 0.583s .416s cubic-bezier(0.96,0.01,0.91,0.26) forwards; */
    /* animation: MoveUpDiv3 5.83s 4.16s ease-in-out forwards; */
    /* animation: MoveUpDiv3 0.43725s .312s ease-in-out forwards; */
}

@keyframes MoveUpDiv3 {
    0% {
        transform: translateY(100%);
    }
    100% {
        transform: translateY(0%);
    }
}


