.mainAnimationEnd {
  display: none;
}

.mainAnimation,
.mainSecondAnimation {
  z-index: 2;

  position: absolute;

  left: 50%;

  width: 1110px;
  height: 1110px;

  border-radius: 50%;

  transform: translateX(-50%);
}

.mainSecondAnimationEnd {
  display: none;
}

.borderAnimation {
  z-index: 3;
  position: absolute;

  top: -250px;
  left: 50%;

  width: calc(100% + 380px);
  height: 1px;

  transform: translateX(-50%) scaleX(0);

  background-color: rgba(255, 255, 255, 0.4);
  /* animation: BorderTransform ease-out 2s 0.2s forwards; */
  animation: BorderTransform ease-out 2s 0.5s forwards;
  /* animation: BorderTransform ease-out 6s 0.5s forwards; */
}

@media (max-width: 1800px) {
  .borderAnimation {
    width: 100%;
  }
}

@media (max-width: 1440px) {
  .borderAnimation {
    top: -180px;
  }
}

@media (max-height: 1100px) {
  .borderAnimation {
    top: -150px;
    top: -130px;
    top: -150px;
  }
}

@media (max-width: 700px) {
  .borderAnimation {
    top: -105px;
  }
}

@keyframes BorderTransform {
  0% {
    transform: translateX(-50%) scaleX(0);
  }
  100% {
    transform: translateX(-50%) scaleX(1);
  }
}

/* центральный черный круг */
.mainAnimation > div:nth-child(1),
.activeSecondAnimation > div:nth-child(1) {
  position: absolute;
  z-index: -1;

  top: 50%;
  left: 50%;

  width: 0;

  transform: translate(-50%, -50%);

  background-color: #ffa500;
  background: rgb(23, 23, 23);

  border-radius: 50%;
  box-shadow: 0px 0px 40px 10x rgba(23, 23, 23, 1);

  width: 150px;
  height: 150px;

  /* animation: InnerCircle ease-out 1.5s 0.2s forwards; */
  animation: InnerCircle ease-out 1.5s 0.5s forwards;
  /* animation: InnerCircle ease-out 4.5s 0.5s forwards; */
}

@media (max-width: 950px) {
  .mainAnimation > div:nth-child(1),
  .activeSecondAnimation > div:nth-child(1) {
    width: 110px;
    height: 110px;
    /* animation: InnerCircleMobile ease-out 1.5s 0.2s forwards; */
    animation: InnerCircleMobile ease-out 1.5s 0.5s forwards;
    /* animation: InnerCircleMobile ease-out 4.5s 0.5s forwards; */
  }
}

@media (max-width: 700px) {
  .mainAnimation > div:nth-child(1),
  .activeSecondAnimation > div:nth-child(1) {
    width: 90px;
    height: 90px;
  }
}

.activeSecondAnimation > div:nth-child(1) {
  background-color: transparent;
}

@keyframes InnerCircle {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    transform: translate(-50%, -50%) scale(10);
  }
}

@keyframes InnerCircleMobile {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    transform: translate(-50%, -50%) scale(13);
  }
}

/* внешний черный круг */
.mainAnimation > div:nth-child(2),
.activeSecondAnimation > div:nth-child(2) {
  position: absolute;

  top: 50%;
  left: 50%;

  width: 1650px;
  height: 1650px;

  border: 730px solid red;
  border-color: #171717;
  border-radius: 50%;
  box-shadow: 0px 0px 40px 10px rgba(23, 23, 23, 1) inset;

  transform: translate(-50%, -50%);
  /* animation: OuterCircle ease-out 2s 0.2s forwards; */
  animation: OuterCircle ease-out 2s 0.5s forwards;
  /* animation: OuterCircle ease-out 6s 0.5s forwards; */
}

@media (max-width: 950px) {
  .mainAnimation > div:nth-child(2),
  .activeSecondAnimation > div:nth-child(2) {
    width: 1150px;
    height: 1150px;
    border-width: 485px;
  }

  .activeSecondAnimation > div:nth-child(2) {
    width: 1550px;
    height: 1550px;
    border-width: 685px;
  }
}

@media (max-width: 700px) {
  .mainAnimation > div:nth-child(2),
  .activeSecondAnimation > div:nth-child(2) {
    width: 900px;
    height: 900px;
    border-width: 375px;
  }

  .activeSecondAnimation > div:nth-child(2) {
    width: 1550px;
    height: 1550px;
    border-width: 715px;
  }
}

@keyframes OuterCircle {
  0% {
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    transform: translate(-50%, -50%) scale(12);
  }
}

/* внутрениий бордер */
.mainAnimation > div:nth-child(3),
.activeSecondAnimation > div:nth-child(3) {
  position: absolute;
  z-index: 3;

  top: 50%;
  left: 50%;

  width: 200px;
  height: 200px;

  border: 4px solid yellow;
  border-color: #3a6ee8;
  border-radius: 50%;

  transform: translate(-50%, -50%);
  /* animation: InnerBorder ease-out 1.1666s 0.2s forwards; */
  animation: InnerBorder ease-out 1.1666s 0.5s forwards;
  /* animation: InnerBorder ease-out 3.498s 0.5s forwards; */
  opacity: 0;
}

@media (max-width: 950px) {
  .mainAnimation > div:nth-child(3),
  .activeSecondAnimation > div:nth-child(3) {
    width: 130px;
    height: 130px;
    /* animation: InnerBorderMobile ease-out 1.1666s 0.2s forwards; */
    animation: InnerBorderMobile ease-out 1.1666s 0.5s forwards;
    /* animation: InnerBorderMobile ease-out 3.498s 0.5s forwards; */
  }
}

@media (max-width: 700px) {
