.wrapper {
    display: flex;
    align-items: center;
    margin-left: auto;
}

.menu {
    display: flex;
    list-style: none;
    
    margin-right: 104px;

    padding-left: 0;

    font-family: 'Inter', sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 90%;
}

@media (max-width: 1199px) {
    .menu {
        margin-right: 40px;
    }
}

@media (max-width: 991px) {
    .menu {
        display: none;
    }
}

.menu a {
    color: #fff;
    text-decoration: none;
    opacity: 0.6;
}

.menu a:hover {
    opacity: 1;
    /* text-decoration: underline; */
}

.menu li {
    margin-right: 50px;
    opacity: 0;
}

@media (max-width: 1199px) {
    .menu li {
        margin-right: 30px;
    }
}

.menu li:nth-child(1) {
    animation: Show 0.25s 0.04s linear;
    animation-fill-mode: forwards;
}
.menu li:nth-child(2) {
    animation: Show 0.25s 0.08s ease-in-out;
    animation-fill-mode: forwards;
}
.menu li:nth-child(3) {
    animation: Show 0.25s 0.12s ease-in-out;
    animation-fill-mode: forwards;
}
.menu li:nth-child(4) {
    animation: Show 0.25s 0.16s ease-in-out;
    animation-fill-mode: forwards;
}
.menu li:nth-child(5) {
    animation: Show 0.25s 0.2s ease-in-out;
    animation-fill-mode: forwards;
}
.menu li:nth-child(6) {
    animation: Show 0.25s 0.24s ease-in-out;
    animation-fill-mode: forwards;
}

@keyframes Show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.menu li:last-child {
    margin-right: 0;
}
