.wrapper {
  flex-shrink: 0;
  pointer-events: none;
}

@media (max-width: 850px) {
  .wrapper {
    display: flex;
    align-items: center;
  }
}

.wrapper:hover .imgBg {
  opacity: 1;
}

.wrapper:hover .contacts {
  opacity: 1;
}

.imageWrapper {
  pointer-events: auto;
  position: relative;
}

.img {
  margin-bottom: 2px;
}

.imgBg {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

@media (max-width: 480px) {
  .img,
  .imgBg {
    max-width: 85px !important;
  }
}

.title {
  font-family: "Inter";
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;
  text-transform: uppercase;

  color: #5498ff;
}

.titleActive {
  color: #696df1;
}

@media (max-width: 1300px) {
  .title {
    margin-bottom: 2px;
    font-size: 18px;
  }
}

@media (max-width: 850px) {
  .title {
    text-align: center;
  }
}

.position {
  margin-top: 0;
  margin-bottom: 8px;

  font-family: "Inter";
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;

  color: #dedefd;

  opacity: 0.6;
}

@media (max-width: 1300px) {
  .position {
    font-size: 14px;
  }
}

@media (max-width: 850px) {
  .position {
    text-align: center;
  }
}

.body {
  pointer-events: auto;
}
@media (max-width: 850px) {
  .body {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
  }
}

.contacts {
  opacity: 0;

  display: flex;
  align-items: center;

  list-style-type: none;

  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;

  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 160%;
  letter-spacing: 0.02em;

  color: #fff;
}

@media (max-width: 850px) {
  .contacts {
    /* display: none; */
  }
}

.contact {
  position: relative;
  margin-right: 10px;
  margin-bottom: 3px;
  cursor: pointer;
}

.contact img {
  display: block;
  width: 23px;
}

.contact:last-child {
  margin-bottom: 0;
}
