    url("./fonts/Autobus/Autobus-Bold.ttf") format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "Trap";
  src: local("Trap"), url("./fonts/Trap/Trap-Medium.ttf") format("truetype");
  font-weight: 500;
}

