  src: local("Inter"), url("./fonts/Inter/Inter-Medium.ttf") format("truetype");
  font-weight: 500;
}

@font-face {
  font-family: "Inter";
  src: local("Inter"),
    url("./fonts/Inter/Inter-SemiBold.ttf") format("truetype");
