.headline {
  position: relative;
  max-width: 1200px;
}

@media (max-width: 767px) {
  .headline {
    position: static;
    /* transform: translateY(-30px); */
  }
}

.h1 {
  display: flex;
  flex-direction: column;

  max-width: 800px;
  margin: 0;

  font-family: "Inter", sans-serif;

  font-style: normal;
  font-weight: bold;
  font-size: 100px;
  line-height: 100%;

  text-transform: uppercase;

  color: #fff;
}

@media (max-width: 1600px) {
  .h1 {
    font-size: 80px;
  }
}

@media (max-width: 1199px) {
  .h1 {
    font-size: 72px;
  }
}

@media (max-width: 991px) {
  .h1 {
    font-size: 60px;
  }
}

@media (max-width: 767px) {
  .h1 {
    font-size: 72px;
    line-height: 115%;
  }
}

@media (max-width: 600px) {
  .h1 {
    font-size: 58px;
  }
}

@media (max-width: 480px) {
  .h1 {
    font-size: 42px;
  }
}

@media (max-width: 359px) {
  .h1 {
    font-size: 38px;
  }
}

.h1 > span {
  overflow: hidden;
}

.h1 > span > span {
  display: block;
  position: relative;
  top: 80px;
  transform: rotate(3deg);
  animation-fill-mode: forwards;
}

.h1 > span:nth-child(1) > span {
  animation: MoveUp 0.4s ease-in-out;
  animation-fill-mode: forwards;
}

.h1 > span:nth-child(2) > span {
  animation: MoveUp 0.4s 0.1s ease-in-out;
  animation-fill-mode: forwards;
}

.h1 > span:nth-child(3) > span {
  animation: MoveUp 0.4s 0.15s ease-in-out;
  animation-fill-mode: forwards;
}

@keyframes MoveUp {
  0% {
    top: 80px;
    transform: rotate(3deg);
  }
  100% {
    top: 0;
    transform: rotate(0deg);
  }
}

.h1 span:nth-child(2) span {
  color: #3a6ee8;
}

.description {
  position: absolute;

  top: 202px;
  left: 455px;

  width: 400px;

  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 130%;

  white-space: pre;

  color: #fff;

  opacity: 0;

  animation: Show 0.4s 0.4s ease-in-out;
  animation-fill-mode: forwards;
}

@media (max-width: 1600px) {
  .description {
    top: 150px;
    left: 360px;
  }
}

@media (max-width: 1199px) {
  .description {
    top: 135px;
    left: 330px;
    font-size: 16px;
  }
}

@media (max-width: 991px) {
  .description {
    top: 110px;
    left: 265px;
    width: 302px;
    font-size: 14px;
    white-space: normal;
  }
}

@media (max-width: 767px) {
  .description {
    position: static;
    top: auto;
    left: auto;
    width: 100%;
    max-width: 400px;
    margin-top: 20px;
    margin-bottom: 0;
    font-size: 18px;
  }
}

@media (max-width: 600px) {
  .description {
    font-size: 16px;
  }
}

@media (max-width: 480px) {
  .description {
    font-size: 18px;
  }
}

@keyframes Show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.description span {
  display: block;
}

@media (max-width: 991px) {
  .description span {
    display: inline;
  }
}
