.wrapper {
    position: relative;
    z-index: 1;
    /* margin-top: -85px; */
}

.row {
    display: flex;
    flex-wrap: wrap;
}

.row:first-child .column:nth-child(odd) {
    background-color: #1A1E2D;
}

.row:first-child .column:nth-child(even) {
    background-color: #162237;
}

.row:last-child .column:nth-child(odd) {
    background-color: #162237;
}

.row:last-child .column:nth-child(even) {
    background-color: #1A1E2D;
}

.column {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 20%;
    /* min-width: 16.666%; */
    height: 230px;
    padding-top: 20px;
}

@media (max-width: 1600px) {
    .column {
        height: 202px;
    }
}

@media (max-width: 1199px) {
    .column {
        height: 180px;
    }
}

@media (max-width: 991px) {
    .column {
        min-width: 33.333%;
        height: 160px;
    }
}

@media (max-width: 767px) {
    .column {
        min-width: 33.333%;
        height: 132px;
    }
}

.column img {
    display: block;
    margin-bottom: 23px;
}

@media (max-width: 1199px) {
    .column img {
        max-width: 65px;
    }
}

@media (max-width: 991px) {
    .column img {
        max-width: 60px;
        margin-bottom: 15px;
    }
}

.column p {
    margin-top: 0;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: 0.02em;

    color: #D8E2FF;
}

@media (max-width: 1199px) {
    .column p {
        font-size: 16px;
    }
}

@media (max-width: 991px) {
    .column p {
        font-size: 14px;
    }
}