.button {
    display: inline-flex;
    justify-content: center;
    align-items: center;

    min-width: 139px;
    height: 37px;

    padding: 4px 30px 2px;

    font-family: 'Inter', sans-serif;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 0.02em;

    background: rgba(58, 110, 232, 0.2);
    border: 1px solid #3A6EE8;
    box-sizing: border-box;
    border-radius: 5px;

    color: #fff;

    transition: all .2s;
    cursor: pointer;
}

.button:hover {
    box-shadow: inset 0 0 8px hsl(0deg 0% 100% / 25%);
    border-color: #04a8f0;
}

@media (max-width: 991px) {
    .button {
        height: 35px;
    }
}