html {
  height: fill-available;
  height: -webkit-fill-available;
}

body * {
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;

  background-color: #171717;
  color: #fff;
}

.container {
  width: 100%;
  max-width: calc(1720px + 200px);
  padding: 0 100px;
  margin: 0 auto;
}

@media (max-width: 1400px) {
  .container {
    padding: 0 30px;
  }
}

@media (max-width: 991px) {
  .container {
    padding: 0 20px;
  }
}

