.wrapper {
    background-color: #2B2B43;
    padding: 58px 0 60px;
}

@media (max-width: 550px) {
    .wrapper {
        padding-bottom: 30px;
    }
}

.row {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
}

.left {
    display: flex;
    flex-direction: column;
    margin-right: 105px;
}

@media (max-width: 360px) {
    .left img {
        width: 90px;
    }
}

@media (max-width: 1600px) {
    .left {
        margin-right: 130px;
    }
}

@media (max-width: 860px) {
    .left {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-right: 0;
        margin-bottom: 40px;
    }
}

.middle {}

@media (max-width: 1199px) {
    .middle {
        margin-left: auto;
    }
}

@media (max-width: 860px) {
    .middle {
        width: 100%;
    }
}

.right {
    margin-left: auto;
    padding-right: 140px;
}

@media (max-width: 1600px) {
    .right {
        padding-right: 70px;
    }
}

@media (max-width: 1199px) {
    .right {
        width: 100%;
        padding-right: 0;
    }
}

.description {
    max-width: 490px;

    margin-top: auto;
    margin-bottom: 2px;

    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 150%;

    color: #FFF;

    opacity: 0.6;
}

@media (max-width: 360px) {
    .description {
        font-size: 12px;
    }
}