.wrapper {
  z-index: 1;

  position: fixed;

  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 100%;
  height: 100%;

  padding-top: 60px;

  background: #1e2840;
  border-radius: 0px;
}

.logo {
  position: absolute;
  top: 26px;
  left: 20px;
}

.logo img {
    width: 75px;
}

.menu {
  display: flex;
  flex-direction: column;
  align-items: center;

  margin-bottom: 100px;
  padding-left: 0;

  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 90%;

  list-style-type: none;
}

.menu li {
  margin-bottom: 40px;
}

.menu li:last-child {
  margin-bottom: 0;
}

.menu a {
  color: #fff;
  text-decoration: none;
}

.controls {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.control {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 235px;
  height: 56px;

  margin-bottom: 30px;

  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 90%;
  text-decoration: none;

  border: 1px solid #3a6ee8;
  border-radius: 5px;

  background: #1c2e58;
  color: #d8e2ff;

  transition: all .2s;
  cursor: pointer;
}

.control:hover {
  box-shadow: inset 0 0 8px hsl(0deg 0% 100% / 25%);
  border-color: #04a8f0;
}

.control:last-child {
  margin-bottom: 0;
  background: #1e2840;
}
