.wrapper {
  display: flex;
  justify-content: center;
  padding-bottom: 100px;
  pointer-events: none;
  /* margin-bottom: 295px; */
}

@media (max-width: 1550px) {
  .wrapper {
    flex-wrap: wrap;
    margin: 0 auto;
  }
}
@media (max-width: 850px) {
  .wrapper {
    flex-wrap: wrap;
    max-width: 600px;
    margin: 0 auto;
  }
}

@media (max-width: 480px) {
