.descriptionMobileOverlay {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.contactsMobile {
  z-index: 1;

  position: fixed;

  bottom: 0;
  left: -1px;

  width: calc(100% + 2px);
  height: 207px;

  padding-top: 20px;

  background: #2a2b43;
  border: 1px solid #696df1;
  border-bottom: none;
  border-radius: 10px 10px 0px 0px;

  text-align: center;
}

.mobileTitle {
  position: relative;

  margin-bottom: 10px;

  font-family: "Inter";
  font-weight: 700;
  font-size: 24px;
  line-height: 100%;

  text-align: center;
  text-transform: uppercase;

  color: #5498ff;
}

.mobileTitle::before {
  content: "";

  position: absolute;
  top: 0;
  left: 50%;

  width: 40px;
  height: 5px;

  background: #696df1;
  border-radius: 5px;

  transform: translate(-50%, -50%) translateY(-32px);
}

.mobilePosition {
  margin: 0;

  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  text-align: center;

  line-height: 100%;

  color: #ffffff;
}

.mobileContacts {
  list-style-type: none;

  display: flex;
  justify-content: center;
  align-items: center;

  margin-top: 35px;
  padding-left: 0;
  pointer-events: auto;
}

.mobileContact {
  margin: 0 10px;
}

.mobileContact img {
  width: 40px;
}
