.tags {
    list-style-type: none;

    margin-top: auto;
    margin-bottom: 0;

    padding: 0;

    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;

    opacity: 0.8;
}

@media (max-width: 1199px) {
    .tags {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        margin-top: 40px;
        padding-top: 40px;
        border-top: 1px solid rgba(255, 255, 255, 0.4);
    }
}

@media (max-width: 550px) {
    .tags {
        margin-top: 10px;
        padding-top: 40px;
    }
}

.tags li {
    margin-bottom: 15px;
}

@media (max-width: 1600px) {
    .tags li {
        margin-bottom: 10px;
        font-size: 16px;
    }
}

@media (max-width: 1199px) {
    .tags li {
        margin-bottom: 15px;
        margin-right: 10px;
        margin-left: 10px;
    }
}

.tags li:last-child {
    margin-bottom: 0;
}

.tags a {
    color: #FFF;
    text-decoration: none;
}

.tags a:hover {
    text-decoration: underline;
}