.three {
  position: relative;
  z-index: 2;
  max-width: 1210px;
  margin: 0 auto;
  /* margin-bottom: 105px; */
}

@media (max-width: 1400px) {
  .three {
    /* margin-bottom: 0; */
    padding-bottom: 350px;
  }
}

@media (max-height: 1200px) {
  .three {
    margin-bottom: -25px;
    /* margin-bottom: 25px; */
  }
}

@media (max-width: 950px) {
  .three {
    padding-bottom: 250px;
  }
}

@media (max-width: 700px) {
  .three {
    padding-bottom: 196px;
  }
}

.nodeRoot {
  display: flex;
  justify-content: center;
}

.nodeRoot > div:nth-child(1) {
  z-index: 3;
}

.node {
  display: flex;
  flex-direction: column;
  margin-bottom: 4px;
}

@media (max-width: 1400px) {
  .node {
    margin-bottom: 30px;
  }
}

@media (max-width: 950px) {
  .node {
    margin-bottom: 20px;
  }
}

.node + .node {
  margin-left: 30px;
}

@media (max-width: 1400px) {
  .node + .node {
  }
  .nodeLeft {
  }
  .dex,
  .zap {
    padding-top: 0 !important;
  }
  .zap {
    margin-left: 150px !important;
  }
}

@media (max-width: 950px) {
  .node + .node {
    margin-left: 10px;
  }
  .zap {
    margin-left: 100px !important;
  }
}

@media (max-width: 700px) {
  .node + .node {
    margin-left: 0px;
  }
  .zap {
    margin-left: 60px !important;
  }
}

.nodeMiddle {
  position: relative;
  margin-left: 50px !important;
  margin-right: 20px;
  padding-top: 121px;
}

.nodeMiddle:hover {
  z-index: 5;
}

@media (max-width: 1400px) {
  .nodeMiddle {
    position: absolute;
    top: 390px;
    left: 50%;
    transform: translateX(-50%);
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding-top: 0;
  }
}

@media (max-width: 950px) {
  .nodeMiddle {
    position: absolute;
    top: 300px;
  }

  .nodeMiddle .children {
    padding-top: 15px !important;
  }
}

@media (max-width: 700px) {
  .nodeMiddle {
    position: absolute;
    top: 254px;
  }

  .nodeMiddle .children {
    padding-top: 0px !important;
  }

  .nodeMiddle .node + .node {
    margin-left: 9px;
  }

  .nodeMiddle .node:nth-child(2),
  .nodeMiddle .node:nth-child(3) {
    padding-top: 25px !important;
  }
}

.row {
  display: flex;
  justify-content: center;
}

.children {
  display: flex;
  padding-top: 40px;
}

@media (max-width: 950px) {
  .children {
    padding-top: 30px;
  }
}

@media (max-width: 700px) {
  .children {
    padding-top: 20px;
  }
}
