.headline {
  display: flex;
  align-items: center;
  padding-top: 22px;
}

.logo {
  opacity: 0;
  animation: Show 0.4s 0.1s linear;
  animation-fill-mode: forwards;
}

@media (max-width: 767px) {
  .logo img {
    width: 74px;
  }
}

@keyframes Show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.button {
  opacity: 0;
  animation: Show 0.2s 0.4s linear;
  animation-fill-mode: forwards;
  text-decoration: none;
}

@media (max-width: 991px) {
  .button {
    order: 2;
    margin-right: 10px;
  }
}

.menuButton {
  z-index: 3;
  position: relative;
  display: none;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  border: 1px solid #fff;
  border-radius: 5px;
  background-color: transparent;
  order: 2;
  cursor: pointer;
}

.menuButton span,
.menuButton span::before,
.menuButton span::after {
  width: 14px;
  height: 2px;
  background-color: #dedefd;
}

.menuButton span {
  position: relative;
}

.menuButton span::before,
.menuButton span::after {
  content: "";
  left: 50%;
  position: absolute;
  transform: translateX(-50%);
}

.menuButton span::before {
  top: -6px;
}

.menuButton span::after {
  bottom: -6px;
}

.menuButtonActive span {
  background-color: transparent;
}

.menuButtonActive span::before,
.menuButtonActive span::after {
  top: 50%;
  left: 50%;
}

.menuButtonActive span::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.menuButtonActive span::after {
  bottom: auto;
  transform: translate(-50%, -50%) rotate(45deg);
}

@media (max-width: 991px) {
  .menuButton {
    display: flex;
  }
}
