.bg::before {
  content: "";
  position: fixed;
  z-index: 3;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 100vh;
  background-color: rgba(23, 23, 23, 0);
  pointer-events: none;
  transition: 0.2s;
  transform: translate(-50%, -50%);
}

.bgActive::before {
  background-color: rgba(23, 23, 23, 0.5);
}

.bgActive .wrapper {
  z-index: 10;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.circle {
  position: relative;

  width: 93px;
  height: 93px;

  margin-bottom: 11px;

  border-radius: 50%;

  border: 1px solid #3a6ee8;
}

@media (max-width: 1400px), (max-height: 980px) {
  .circle {
    width: 73px;
    height: 73px;
  }
}

@media (max-width: 950px) {
  .circle {
    width: 63px;
    height: 63px;
  }
}

@media (max-width: 700px) {
  .circle {
    width: 60px;
    width: 50px;
    height: 50px;
    margin-bottom: 8px;
  }
}

.circle:hover ~ .title {
  opacity: 0;
}

.circle:hover ~ .description {
  opacity: 1;
}

.circle:hover ~ .description span:last-child {
  opacity: 0.6;
}

.circle:hover .waves::before {
  opacity: 0.2;
}

.circle:hover .waves::after {
  opacity: 0.05;
}

.waves {
  position: absolute;

  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}

.waves::before,
.waves::after {
  content: "";

  position: absolute;

  top: 50%;
  left: 50%;

  border: 1px solid #3a6ee8;
  border-radius: 50%;

  transform: translate(-50%, -50%);

  opacity: 0;
}

.waves::before {
  width: 96px;
  height: 96px;
}

@media (max-width: 950px) {
  .waves::before {
    width: 86px;
    height: 86px;
  }
}

@media (max-width: 700px) {
  .waves::before {
    width: 66px;
    height: 66px;
  }
}

.waves::after {
  width: 156px;
  height: 156px;
}

@media (max-width: 1400px), (max-height: 980px) {
  .waves::after {
    width: 136px;
    height: 136px;
  }
}

@media (max-width: 950px) {
  .waves::after {
    width: 126px;
    height: 126px;
  }
}

@media (max-width: 700px) {
  .waves::after {
    width: 116px;
    height: 116px;
  }
}

.circleActive + .title {
  color: #fff;
}

.circleActive::after {
  background-color: #3a6ee8;
}

.circle::after {
  content: "";

  position: absolute;

  top: 50%;
  left: 50%;

  width: 60px;
  height: 60px;

  border-radius: 50%;

  transform: translate(-50%, -50%);
}

@media (max-width: 1400px), (max-height: 980px) {
  .circle::after {
    width: 42px;
    height: 42px;
  }
}

@media (max-width: 950px) {
  .circle::after {
    width: 39px;
    height: 39px;
  }
}

@media (max-width: 700px) {
  .circle::after {
    width: 30px;
    height: 30px;
  }
}

.dot {
  position: absolute;

  width: 9px;
  height: 9px;

  border-radius: 50%;

  background: #3a6ee8;
}

@media (max-width: 700px) {
  .dot {
    width: 5px;
    height: 5px;
  }
}

.title {
  width: 120px;

  margin-top: 0;
  margin-bottom: 0;

  font-weight: normal;
  font-size: 22px;
  line-height: 90%;

  text-align: center;

  color: rgba(255, 255, 255, 0.4);

  opacity: 1;

  transition: opacity 0.2s;
}

@media (max-width: 950px) {
  .title {
    width: 92px;
    font-size: 16px;
  }
}

@media (max-width: 700px) {
  .title {
    width: 60px;
    font-size: 12px;
  }
}

.description {
  position: absolute;
  z-index: 3;

  top: 85px;
  left: 50%;

  height: 108px;
  width: 473px;

  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;

  text-align: center;

  color: #fff;

  opacity: 0;
  text-shadow: 0px 0px 50px #171717;

  transform: translateX(-50%);

  pointer-events: none;

  transition: opacity 0.2s;
}

@media (max-width: 1550px) {
  .description {
    width: 300px;
  }
}

@media (max-width: 1050px) {
  .description {
    width: 350px;
    font-size: 16px;
  }
}

@media (max-width: 950px) {
  .description {
    top: 90px;
    width: 300px;
    font-size: 14px;
  }
}

@media (max-width: 700px) {
  .description {
    top: 70px;
    width: 250px;
  }
}

