.descriptionMobile {
  position: fixed;

  z-index: 15;

  top: auto;
  bottom: 0;
  left: -1px;

  width: calc(100% + 2px);

  margin: 0;
  padding: 45px 20px 50px;

  text-align: center;
  line-height: 140%;

  background: #2a2b43;
  border: 1px solid #696df1;
  border-bottom: none;
  border-radius: 10px 10px 0px 0px;

  transform: none;
}

.descriptionMobile span:last-child {
  color: rgba(255, 255, 255, 0.4);
}

.descriptionMobile::before {
  content: "";

  position: absolute;
  top: 10px;
  left: 50%;

  width: 40px;
  height: 5px;

  background: #696df1;
  border-radius: 5px;

  transform: translateX(-50%);
}

