.relativeContainer {
  position: relative;
  z-index: 1;
  width: 363px;
  height: 283px;

  width: 465px;
  height: 335px;

  margin: 0 auto;
}

@media (max-width: 450px) {
  .relativeContainer {
    width: 100%;
  }
}

.wrapper {
  position: relative;

  display: flex;

  justify-content: flex-start;
  align-items: center;

  /* width: 363px; */
  /* height: 363px; */

  width: 465px;
  height: 335px;

  margin: 0 auto;

  text-align: center;

  /* border: 1px solid rgba(58, 110, 232, 0.4); */
  /* border-radius: 50%; */

  background-image: url('./second-stage.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

@media (max-width: 450px) {
  .wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

