.inner {
  /* width: 321px; */
  /* height: 321px; */

  margin: 0 auto;

  /* border: 1px solid #3a6ee8; */
  
  border-radius: 50%;
}

.title {
  z-index: 2;

  position: relative;

  margin-top: 0;
  /* padding-top: 35px; */

  font-size: 24px;
  font-weight: bold;
  line-height: 90%;
}

@media (max-width: 700px) {
  .title {
    padding-top: 105px;
    font-size: 18px;
  }
}

.wrapper:hover .description {
  opacity: 1;
}

.wrapper:hover .description span:last-child {
  opacity: 0.6;
}

.description {
  z-index: 2;

  opacity: 0;

  position: absolute;

  top: 163px;
  left: 50%;

  height: 108px;
  width: 473px;

  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;

  text-align: center;

  color: #fff;

  opacity: 0;
  /* border: 1px solid #171717; */
  text-shadow: 0px 0px 50px #171717;

  transform: translateX(-50%);
}

@media (max-width: 700px) {
  .description {
    top: 0;
  }
}

.descriptionMobileOverlay {
  position: fixed;
  z-index: 15;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.descriptionMobile {
  position: fixed;

  z-index: 15;

  top: auto;
  bottom: 0;
  left: -1px;

  width: calc(100% + 2px);

  margin: 0;
  padding: 45px 20px 50px;

  text-align: center;
  line-height: 140%;

  background: #2a2b43;
  border: 1px solid #696df1;
  border-bottom: none;
  border-radius: 10px 10px 0px 0px;

  transform: none;
}

.descriptionMobile span:last-child {
  opacity: 0.6;
}

.descriptionMobile::before {
  content: "";

  position: absolute;
  top: 10px;
  left: 50%;

  width: 40px;
  height: 5px;

  background: #696df1;
  border-radius: 5px;

  transform: translateX(-50%);
}
