.title {
  position: relative;
  z-index: 5;
  margin-top: 0;
  margin-bottom: 88px;

  font-weight: 600;
  font-size: 72px;
  line-height: 100%;

  text-align: center;
  text-transform: uppercase;

  color: #3a6ee8;
}

.activeAnimate {
  animation: ShowTitle 1s ease-out forwards;
}

@keyframes ShowTitle {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
  }
}

@media (max-width: 1400px) {
  .title {
    margin-bottom: 50px;
    font-size: 62px;
  }
}

@media (max-width: 950px) {
  .title {
    margin-bottom: 50px;
    font-size: 56px;
  }
}

@media (max-width: 700px) {
  .title {
    margin-bottom: 40px;
    font-size: 36px;
  }
}

@media (max-height: 1100px) {
  .title {
    margin-bottom: 40px;
  }
}