.title {
    margin-bottom: 90px;

    font-family: 'Inter';
    font-size: 72px;
    font-weight: 700;
    line-height: 80%;

    /* letter-spacing: 0.1em; */

    text-align: center;
    text-transform: uppercase;

    color: #3A6EE8;
}

@media (max-width: 1300px) {
    .title {
        margin-bottom: 70px;
        font-size: 56px;
    }
}

@media (max-width: 850px) {
    .title {
        margin-bottom: 65px;
        font-size: 36px;
    }
}

@media (max-width: 480px) {
    .title {
        margin-bottom: 35px;
    }
  }