.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100%;
  padding-top: 60px;
  /* background-image: url('./promo.png'); */
  /* background-image: url('./promo-mobile.png'), url('./overlay-mobile.png'); */
  background-image: url("./promo.jpg");
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}

@media (max-width: 650px) {
  .wrapper {
    background-image: url("./promo-mobile.jpg");
    background-size: 100% auto;
    background-position: top center;
    background-repeat: no-repeat;
  }
}

.wrapper p {
  max-width: 890px;
  margin: 0 auto 40px;
  font-family: "Inter";
  font-weight: 500;
  font-size: 36px;
  line-height: 1.28;
  text-align: center;
  color: #888a92;
}

@media (max-width: 1199px) {
  .wrapper p {
    max-width: 800px;
    font-size: 32px;
  }
}

@media (max-width: 991px) {
  .wrapper p {
    max-width: 650px;
    font-size: 26px;
  }
}

@media (max-width: 767px) {
  .wrapper p {
    max-width: 600px;
    font-size: 24px;
  }
}

.wrapper p:last-child {
  margin-bottom: 0;
}

.wrapper p span {
  color: #fff;
}
