.wrapper {
    display: flex;
    align-items: center;

    font-weight: 500;
    font-size: 14px;

    line-height: 130%;
    text-align: right;

    color: #FFF;

    opacity: 0;
    animation: Show 0.2s 0.4s linear;
    animation-fill-mode: forwards;
}

@media (max-width: 991px) {
    .wrapper {
        font-size: 16px;
    }
}

@media (max-width: 767px) {
    .wrapper {
        display: none;
    }
}

.wrapper span {
    margin-right: 10px;
}

@media (max-width: 991px) {
    .wrapper span {
        margin-right: 5px;
    }
}

.wrapper img {
    margin-bottom: 6px;
}

@media (max-width: 991px) {
    .wrapper img {
        margin-bottom: 3px;
    }
}

@keyframes Show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
